import AppHome from '@/components/Home';
const Ign = () => import('@/components/Ign');
const Fitted = () => import('@/components/Fitted');
const InkTank = () => import('@/components/InkTank');
const GoKards = () => import('@/components/GoKards');
const Art = () => import('@/components/Art');
const AboutMe = () => import('@/components/AboutMe');

const routes = [
    {
        path: '/',
        name: 'Home',
        component: AppHome
    },
    {
        path: '/fitted',
        name: 'Fitted',
        component: Fitted
    },
    {
        path: '/about_me',
        name: 'About Me',
        component: AboutMe
    },
    {
        path: '/art',
        name: 'Art',
        component: Art
    },
    {
        path: '/ign',
        name: 'Ign',
        component: Ign
    },
    {
        path: '/inktank',
        name: 'Ink Tank',
        component: InkTank
    },
    {
        path: '/gokards',
        name: 'Go Kards',
        component: GoKards
    },
];

export default routes;



