<template>
  <div class="col-100">
    <div class="img-container" :class="classnames">
      <img v-lazy="src" :alt="alt" />
    </div>
  </div>
</template>

<script>
import Width100 from './layout/Width100';

export default {
  name: 'VueImage',
  components: {
    Width100,
  },
  props: {
    src: {
      type: String,
    },
    alt: {
      type: String,
      default: "image"
    },
    classnames: {
      type: String,
      default: ""
    },
  }
}
</script>

<style lang="scss" scoped>
.img-container {
  text-align: center;

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}
</style>



