<template>
<div>
<Width100>
    <h1 id="project-header" class="project_header_title">Projects</h1>

  </Width100>
  <Width100>
    <Width50>
      <ProjectCard
        title="IGN"
        type="Redesign"
        description="User Interface redesign of IGN's Esports page."
        project_url="/ign"
        :project_img="ignCover"
        background_color="#d87a79"
        id="ign-project"
      />
    </Width50>
    <Width50>
      <ProjectCard
        title="Fitted"
        type="Case Study"
        description="Fitted was designed to motivate people into an exercise routine that suits their level, schedule, and interests."
        project_url="/fitted"
        :project_img="fittedCover"
        background_color="#492d75"
        id="fitted-project"
      />
    </Width50>
  </Width100>
  <Width100>
    <Width50>
      <ProjectCard
          title="GoCards"
          type="Case Study"
          description="GoCards was designed to enhance the learning experience of memorizing and understanding new concepts, techniques, and terms. "
          project_url="/gokards"
          :project_img="goKardsCover"
          background_color="#563bb0"
          id="gokards-project"
        />
    </Width50>
    <Width50>
      <ProjectCard
          title="Ink Tank"
          type="Case Study"
          description="Ink Tank was designed to look for tattoo inspiration according to preferences and to find the perfect artist to execute the design."
          project_url="/inktank"
          :project_img="inkTankCover"
          background_color="#4990E2"
          id="gokards-project"
        />
    </Width50>
  </Width100>
</div>
</template>

<script>
import ProjectCard from './ProjectCard';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import ignCover from '../../assets/img/ign_mockup.png';
import fittedCover from '../../assets/img/fitted_mockup2.png';
import inkTankCover from '../../assets/img/inktank_thumbnail.png';
import goKardsCover from '../../assets/img/gocards_thumbnail.svg';

export default {
    name: 'ProjectsList',
    components: {
      ProjectCard,
      Width50,
      Width100
    },
    data: function() {
      return {
        ignCover: ignCover,
        fittedCover: fittedCover,
        goKardsCover,
        inkTankCover,
      }
    }
};
</script>

<style lang="scss" scoped>
.project_header_title {
  font-size: 64px;
  font-weight: 400;
  margin: 16px auto;
  text-align: center;
}
</style>



