<template>
<div :id="id" class="col-100 container" >
  <div class="href-container" v-bind:style="{'background-color': background_color}">
    <router-link :to="project_url" @click.native="inlineButtonClickHandler">
      <div class="text-container">
        <h2 class="project-heading">{{title}}</h2>
        <h3 class="project-type">{{type}}</h3>
      </div>
      <div class="img-container">
        <img :src="project_img" :alt="title">
      </div>
    </router-link>
  </div>
</div>
</template>

<script>
export default {
    name: 'ProjectCard_v2',
    props: {
      'id': String,
      'title': String,
      'type': String,
      'description': String,
      'project_url': String,
      'project_img': String,
      'background_color': String
    },
    methods: {
      inlineButtonClickHandler: function() {
        this.$ga.event('home', 'click', 'project-card', this.title);
      }
    }
};
</script>

<style lang="scss" scoped>
.container {
  padding: 16px;
}

.href-container {
  max-width: 650px;
  margin: 0 auto;
  border-radius: 8px;
  overflow: hidden;

  a {
    text-decoration: none;
  }

  &:hover img {
    transform: scale(1.2);
  }
}

.img-container {
  overflow: hidden;

  img {
    width: 100%;
    height: 380px;
    display: block;
    transition: transform .6s ease;
    padding: 8px;
  }
}

.text-container {
  color: white;
  padding: 16px;

  .project-heading {
    font-weight: 600;
    font-size: 40px;
    line-height: 1;
    margin-bottom: 16px;
  }
  .project-type {
    font-weight: 300;
    font-size: 24px;
    line-height: 1;
    margin-bottom: 16px;
  }
  .description {
    line-height: 1.5;
    font-size: 19px;
    margin-bottom: 36px;
  }
}

@media screen and (max-width: 900px) {
  .img-container {
    img {
      height: auto;
    }
  }
}
</style>



