<template>
<div class="col-100 container" :class="classnames" :style="styles">
  <div class="center">
    <slot></slot>
  </div>
</div>
</template>

<script>
export default {
  name: 'Width100',
  props: {
    addPadding: Boolean,
    fullHeight: Boolean,
    classnames: {
      type: String,
      default: "",
    },
    backgroundColor: String,
  },
  computed: {
    styles: function() {
      return {
        padding: this.addPadding ? '32px' : '0',
        "background": this.backgroundColor ? this.backgroundColor : "white",
        "background-repeat": "no-repeat",
        "height": this.fullHeight ? "100vh" : "",
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  margin: 0 auto;
}

@media (min-width: 480px) {
  .center {
    display: block;
  }
}

@media (min-width:600px)  {
  .center {
    display: flex;
    flex: 1 1 auto;
  }
}

@media (min-width:1280px) {
  .center {
    max-width: 1280px;
    margin: 0 auto;
  }
}

</style>



