<template>
<div class="footer">
  <h1 class="contact_description_icon">Connect with me!</h1>

  <div class="icons_container">
    <a class="icon" href="mailto:maihannguyen14@gmail.com" v-on:click="inlineButtonClickHandler">
      <VueImage
        :src="mail_icon"
        alt="mail icon"
      />
    </a>
    <a class="icon" href="https://www.linkedin.com/in/mai-nguyen-6a3745b8/" v-on:click="inlineButtonClickHandler">
      <VueImage
        :src="linkedin_icon"
        alt="linkedin icon"
      />
    </a>
    <a class="icon" href="https://dribbble.com/mai_nguyen" v-on:click="inlineButtonClickHandler">
      <VueImage
        :src="dribble_icon"
        alt="dribble icon"
      />
    </a>
  </div>

  <p class="footer_description">Copyright  Mai Nguyen. All rights reserved.</p>
</div>
</template>


<script>
import Lottie from './components/common/Lottie';
import VueImage from './components/common/VueImage';

import mail_icon from "./assets/img/email.svg";
import linkedin_icon from "./assets/img/linkedin.svg";
import dribble_icon from "./assets/img/dribble.svg";

export default {
  components: {
    VueImage,
  },
  data() {
    return {
      mail_icon,
      linkedin_icon,
      dribble_icon,
    }
  },
  methods: {
    scrollTo(selector) {
      const el = document.querySelector(selector);
      el.scrollIntoView({ behavior: 'smooth' });
      this.$ga.event('footer', 'click', 'scrollToTop', 1);
    },
    inlineButtonClickHandler: function(e) {
      if (e.srcElement) {
        this.$ga.event('footer', 'click', e.srcElement.alt);
      }
    },
    handleAnimation: function (anim) {
      this.anim = anim;
    },
  }
}
</script>

<style lang="scss" scoped>
.footer {
  padding: 16px;
  background: #e76d89;
  color: white;
  text-align: center;
  display: inline-block;
  width: 100%;

  .icons_container {
    display: flex;
    justify-content: center;
    margin: 16px 0;

    .icon {
      margin: 0 8px;
      border-radius: 100%;
      height: 64px;
      width: 64px;
      padding: 16px;
      border: 2px solid white;
      cursor: pointer;
    }
  }

  h1.contact_description_icon {
    margin-bottom: 16px;
    font-size: 36px;
    font-weight: 400;
  }
}

</style>



