<template>
<Width100 v-bind:addPadding="true">
    <div class="header">
      <div class="col-100">
        <div class="col-50 info-container">
          <h1 class="name noselect">Hello! I'm <router-link to="/about_me">Mai Nguyen</router-link></h1>
          <p class="intro noselect">I am a UX designer who loves learning about user behavior and researching how technology can be utilized to fulfill a wide range of human needs.</p>
          <p class="social noselect">You can find me on <a href="https://www.linkedin.com/in/mai-nguyen-6a3745b8/">Linkedin</a> and <a href="https://dribbble.com/mai_nguyen">Dribbble</a>. </p>
        </div>
        <div class="col-50">
          <div class="grill_container">
            <div class="grill"
              @mouseover="hover = true"
              @mouseleave="hover = false">
              <img
                :src="grill1"
                alt="Mai Nguyen"
                v-bind:class="{hidden: hover}">
              <img
                :src="grill2"
                alt="Mai Nguyen"
                v-bind:class="{hidden: !hover}">
            </div>
          </div>
        </div>
      <div class="noselect arrow-container col-100">
        <button v-on:click="scrollTo('#project-header')">
          <lottie
          :options="animationOptions"
          :height="100"
          :width="100"
          v-on:animCreated="handleAnimation"
          />
        </button>
        <LineBreak />
        </div>
      </div>
    </div>
</Width100>
</template>

<script>
import Width100 from '../common/layout/Width100';
import animationData from '../../animations/arrow.json';
import Lottie from '../common/Lottie';
import LineBreak from '../common/LineBreak';

import grill1 from "../../assets/img/basic_grill_1.svg";
import grill2 from "../../assets/img/basic_grill_2.svg";

export default {
  name: 'LandingV2',
  components: {
    Lottie,
    LineBreak,
    Width100
  },
  data() {
    return {
      animationOptions: {
        animationData: animationData,
        animationSpeed: 1,
      },
      hover: false,
      grill1: grill1,
      grill2: grill2
    };
  },
  methods: {
    scrollTo(selector) {
      const el = document.querySelector(selector);
      el.scrollIntoView({ behavior: 'smooth' });
      this.$ga.event('home', 'click', 'scrollToProjects', 1);
    },
    handleAnimation: function (anim) {
      this.anim = anim;
    },
  }
};
</script>

<style lang="scss" scoped>
.header {
  width: 100%;
  grid-area: header;
  position: relative;
  margin: 0 auto;
  padding-top: 32px;
  overflow: hidden;

  .arrow-container {
    padding-top: 32px;
    text-align: center;
    background-color: transparent;

    button {
      background-color: transparent;
      transform: rotate(90deg);

      &:hover {
        cursor: pointer;
      }
    }
  }

  .grill_container {
    text-align: center;
    margin: 0 auto;
    .grill {
      width: 100%;
      height: 100%;
      position: relative;
      margin: 0 auto;
    }
  }

  .info-container {
    padding: 16px;

    .name {
      font-size: 48px;
      color: black;
      margin-bottom: 16px;
      a {
        color: #e76d89;
      }
    }

    p {
      padding-left: 4px;
    }
    .intro {
      margin-top: 20px;
      font-size: 25px;
    }
    .social {
      margin-top: 16px;
      font-size: 20px;
      color: #484848;

      a {
        color: #e76d89;
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .header {
    padding: 0;
    .info-container {
      text-align: center;

      .name {
        a {
          display: block;
        }
      }
      p {
        padding: 0;
      }
    }

    .grill_container {
      margin-top: 32px;
      width: 100%;

      .grill img {
        width: 100%;
        max-height: 350px;
      }
    }
  }
}
</style>



