<template>
  <div id="project-">
    <Width100 :addPadding="true">
      <div id="project-header" />
      <lottie
        :options="defaultOptions"
        :height="320"
        v-on:animCreated="handleAnimation"
      />
    </Width100>
  </div>
</template>

<script>
import Width100 from '../common/layout/Width100';
import Lottie from '../common/Lottie';
import animationData from '../../animations/duck.json';

export default {
  components: {
    Lottie,
    Width100
  },
  data() {
    return {
      defaultOptions: {animationData: animationData},
      animationSpeed: 1
    }
  },
  methods: {
    handleAnimation: function (anim) {
      this.anim = anim;
    },
  }
};
</script>

<style lang="scss" scoped>
#project-header {
  max-height: 400px;
}
</style>



